.main_container{
    /*position: relative;*/
    display: flex;
    flex-direction: column;
    width: 95%;
    /*background: red;*/
}
.tab_container{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    border: 1px solid  #d8d8d8;
    box-shadow: 1px 2px 3px black;
    top: -125px;
    border-radius: 8px;
    padding: 2px;
    background: rgba(187, 187, 187, 0.65);

}
.table {
    width: 100%;
    border: none;
    margin-bottom: 20px;
}
.table thead th {
    font-weight: bold;
    text-align: left;
    border: none;
    padding: 10px 15px;
    background: rgba(124, 245, 160, 0.6);
    font-size: 14px;
}
.table thead tr th:first-child {
    border-radius: 8px 0 0 8px;
}
.table thead tr th:last-child {
    border-radius: 0 8px 8px 0;
}
.table tbody td  {
    text-align: left;
    border: none;
    padding: 10px 15px;
    font-size: 11px;
    vertical-align: top;
}
.table tbody tr:nth-child(even){
    background: #f3f3f3;
}
.table tbody tr td:first-child {
    border-radius: 8px 0 0 8px;
}
.table tbody tr td:last-child {
    border-radius: 0 8px 8px 0;
}
